.footer{
    background: rgb(32, 32, 32);
    color: white;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    padding: 3rem;
}

#country{
    display: grid;
    grid-template-rows: repeat(4,1fr);
}

#insideCountry{
    grid-row-start: 2;
}
li{
    list-style: none;
    margin: 1rem 0;
    color: #B6BDC4;
}