* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

/* styling navlist */
.navbar{
    background: #212731;
    height: 4.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
#navlist {
	display: flex;
    align-items: center;
    justify-content: space-around;
    width: 70%;
}

#navlist a {
	float: left;
	display: block;
	color: #f2f2f2;
	text-align: center;
	padding: 12px;
	text-decoration: none;
	font-size: 15px;
}

#searchIcon{
    position: absolute;
    font-size: 2rem;
}

.search input[type=text] {
	width: 300px;
	height: 40px;
	border-radius: 5px;
    text-align: center;
	border: none;
}

#bar{
    display: flex;
    align-items: center;
    /* justify-content: center; */
}
.search {
	display: flex;
    align-items: center;
    justify-content: center;
}

.search button {
	color: #f2f2f2;
	float: right;
}
